
.hidden {
    display: none;
}
.clearfix {
    clear: none;
}
a {
    cursor:pointer;
}
.activeKeyword {
    color: red;
}
.stars {
    float: right;
}
.repository {
    margin: 5px;
}
.content {
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 5px;
    border: 1px darkgray solid;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
.header {
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 5px;
    background: darkgray;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.loading {
    cursor:wait;
}
.sidebar {
    float: left;
    width: 10%;
    padding-top: 5px;
}
.keywords {
    padding: 5px;
    border: 1px gray solid;
    border-radius: 5px;
}
.main {
    float: left; width: 90%
}



